<template>
	<view class="person_wrap">
		<template v-if="cusList.length > 0">
			<view class="personList" v-for="(item,index) in cusList" :key="index" @click="goDet(item)">
				<view class="listTop">
					<view class="listLeft">
						<view class="nameTag">
							<text class="custname">{{item.custName}}</text>
							<text class="tags" v-if="item.intensionLevelName">{{item.intensionLevelName}}</text>
							<text class="tags bule" v-if="item.stateName">{{item.stateName}}</text>
						</view>
						<view class="custMobile">{{item.custPhone}}</view>
					</view>
				</view>
				<view class="listBottom">
					<view class="bottomType">
						<text class="typeNameTag">{{item.behaviorName}}</text>
						<text class="contentText">{{item.content}}</text>
					</view>
					<text class="time">{{item.followExcuteTime}}</text>
				</view>
			</view>
		</template>
		<template v-else>
			<image class="noData" src="@/static/img/noData.png" mode=""></image>
		</template>
		<selectTemplate ref="selectTemplate"></selectTemplate>
	</view>
</template>

<script>
	import selectTemplate from '@/pages/salemanVisual/customer/components/selectTemplate.vue';
	export default {
		props: ['cusList'],
		data() {
			return {}
		},
		methods: {
			// 问卷列表
			goDet(item) {
				this.$refs.selectTemplate.open(item.id)
				// uni.navigateTo({
				// 	url: '/pages/salemanVisual/customer/questionList?id=' + item.id
				// })
			},
		},
		components:{
			selectTemplate
		}
	}
</script>

<style lang="scss">
	@import '@/styles/personList.scss';
	.person_wrap{
		padding-top: 36rpx;
	}
</style>
